.footer-section {
  background-color: var(--secondary-black);
  flex-direction: row;
  justify-content: space-between;
  padding: 5rem;
  display: flex;
}

.imgHolder {
  justify-content: center;
  align-items: center;
  width: 23%;
  display: flex;
}

.imgHolder img {
  width: 90%;
}

.footer-section .footer-title {
  width: -moz-fit-content;
  width: fit-content;
  font-size: 1.7rem;
  position: relative;
}

.footer-section .footer-title:after {
  content: "";
  background-color: var(--primary-blue);
  border-radius: 8px;
  width: 100%;
  height: 5px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.docs ul {
  padding-top: 1rem;
  list-style: none;
}

.docs ul li {
  padding-top: .7rem;
}

.docs ul li a, .contactUs .contactList a {
  color: var(--white);
  letter-spacing: .08em;
  text-shadow: -.25px -.25px #0000, .25px .25px #0000;
  font-size: 1.2rem;
  text-decoration: none;
  transition: all .25s ease-out;
  position: relative;
}

.docs ul li a:hover, .contactUs .contactList a:hover {
  color: var(--primary-blue);
  text-shadow: -.25px -.25px 0 var(--primary-blue), .25px .25px var(--primary-blue);
}

.social-links {
  padding-top: 2rem;
}

.social-links a {
  color: var(--white);
  padding-right: 1rem;
  font-size: 2rem;
  transition: all .3s;
}

.social-links a:hover {
  color: var(--primary-blue);
}

.contactList, .contactList h4 {
  padding-top: 1rem;
}

.contactUs .contactList a {
  font-size: .9rem;
}

.footer-ke-neeche {
  background-color: var(--secondary-black);
  color: #ffffffb3;
  text-align: center;
  line-height: 3rem;
}

@media only screen and (max-width: 1200px) {
  .footer-section {
    padding: 1rem;
  }

  .footer-section .footer-title {
    font-size: 1.5rem;
  }
}

@media only screen and (max-width: 768px) {
  .footer-section {
    text-align: center;
    flex-direction: column;
    padding: 2rem;
  }

  .footer-section .footer-title {
    width: 100%;
    font-size: 2rem;
  }

  .footer-section .footer-title:after {
    height: 1.5px;
  }

  .imgHolder {
    display: none;
  }

  .docs, .socials, .contactUs {
    padding-top: 1rem;
  }
}

@media only screen and (max-width: 1000px) {
  .imgHolder {
    display: none;
  }
}

